
.text{
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Audiowide';
}

.welcomeMain{
    background-color: black;

    color: #E8E8E8;
    position: relative;
}

.allText{
    text-align: center;
    margin: 20px 3% 60px 3%;
    /*background-color: #282828E8;*/
    z-index: 1;
}



.headText{
    text-align: center;
    margin: 50px 3% 50px 3%;

}

.bodyPara{
    margin: 50px 3% 50px 3%;
    margin-bottom: calc(2vw);
    line-height: 2em;
    white-space: pre-wrap;
    padding: 20px 0;
}

#background{
    position: fixed;
    top: 0;
    left: 0;
    
}

