iframe{
    z-index: 1;
}
  
/* .video-responsive iframe {
    max-width: 100%;
} */

.bgBorder{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    filter: opacity(0.3);
}

.cardBG{
    position: relative;
    z-index: -1;
    width: 120%;
    height: 300%;
    top: -200%;
}

.layer{
    width:100%;
    height:100%;
}

.card{
    color:#E8E8E8;
}

.cardHolder{
    position: absolute;
    z-index: -1;
}


.player-wrapper {
    position: relative;
    padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
    margin: 50px 2px;
}
  
.react-player {
    position: absolute;
    top: 0;
    left: 0;
}

.wave{
    position: relative;
    bottom: 0;
}
@media screen and (max-width: 500px) {
    .allText {
        font-size: 14px;
    }
    .headText{
        margin: 50px 0% 50px 0%;
    
    }
    
    .bodyPara{
        margin: 15px 0% 15px 0%;
    }

    #text6 {
        font-size: 16px;
    }
    
}

#text6 {
    font-size: 20px;
}